import React from 'react';
import ParserHTML from '../components/particles/ParseHTML';
import { graphql } from 'gatsby';
import Image from 'gatsby-image';
import Layout from './layout';
import Meta from './meta';


export const query = graphql`
    query($slug: String!) {
        allDatoCmsArticulo(filter: { slug: { eq: $slug } }) {
            nodes {
                    titulo
                    id
                  descripcion
                  meta {
                    updatedAt
                  }
                  seoMetaTags {
                    tags
                  }
                categoria
                  tag
                  imgdestacada {
                    fluid {
                        ...GatsbyDatoCmsFluid
                    }
                }
            }
        }
    }
`

const ArticuloTemplate = ({ data: { allDatoCmsArticulo: { nodes } } }) => {

    const { imgdestacada, descripcion, titulo, seoMetaTags } = nodes[0];

    return (
        <Layout>
            <Meta
                title={seoMetaTags.tags[0].content}
                description={seoMetaTags.tags[3].attributes.content}
                image={seoMetaTags.tags[13].attributes.content}
            />
            <main className="w-2/3 mx-auto my-8 list-none overflow-hidden">
                <div itemscope="" itemtype="http://schema.org/Article">

                    <div className="overflow-hidden relative">
                        <div className="before:bg-black before:absolute before:inset-0">
                            <Image itemProp="image" className="w-full h-192	mx-auto opacity-70 "
                                fluid={imgdestacada.fluid}
                            />
                    <h1 itemprop="name" className="text-center m-8 absolute text-white font-bold top-2/4 inset-x-0">{titulo}</h1>
                        </div>
                    </div>
                    <article className="m-12" >{ParserHTML(descripcion)} </article>
                </div>
                {/* <br></br><br></br><b>Tag: </b>{tag}
                <br></br><br></br><b>Categoria: </b>
            <b>{categoria}</b> */}
            </main>
        </Layout>
    );
}

export default ArticuloTemplate;